import {
    gIsSPA,
    gAppId,
    gEndPoint,
    gServerType,
    gPageId,
    gAppInfoData,
    setPrevPageData, gPrevPageData, gDeviceInfo
} from "../globals";
import {makeSendData, send} from "../utils/sender";
import {createPageId, getOrCreateSessionUUID} from "../utils/uuid";
import {maxyLogType} from "../utils/logType";
import { getComType } from "../utils/device";
import {
    getLoginYn, getPageErrCnt, getPageEvtCnt,
    getPageFlowOrder, getPageLogCnt, getPageReqCnt, getPageReqTotal,
    getParentLogDate,
    getUserIdInMaxy, setPageErrCnt, setPageEvtCnt,
    setPageFlowOrder, setPageLogCnt, setPageReqCnt, setPageReqTotal
} from "../utils/storage";

const KEY_MAXY_PGIF = `mf-pgif-${gAppId}-${gServerType}`;

/**
 * SPA 라우터 조작 메서드(history.pushState / replaceState)를 패치하여
 * 라우터 이동 시점에 이벤트를 전송합니다.
 *
 * @param {'pushState' | 'replaceState'} method - 후킹할 history 메서드
 */
function patchHistoryMethod(method: 'pushState' | 'replaceState') {
    const original = history[method] as (data: any, unused: string, url?: string | URL | null) => void;

    history[method] = function (data: any, unused: string, url?: string | URL | null) {
        // 이동 이벤트 전송
        if (gIsSPA) {
            const pageStartTime = Math.floor(performance.timeOrigin + performance.now()); // 또는 Date.now()
            let pageUrl: string = '';

            /*if (url === null || url === undefined || url === '') {
                if (unused === null || unused === undefined || unused === '') pageUrl = data;
                else pageUrl = unused;
            } else pageUrl = url;
            */
            if (url === null) {
                console.error(`[MF] pushState`)
                return;
            }

            // 중복방지 추가
            if (typeof url === 'string') {
                pageUrl = url;
            } else if (url instanceof URL) {
                pageUrl = url.href;
            }
            //console.log(`pushState-path ${pageUrl}`);

            const current = location.pathname + location.search + location.hash;
            // URL 상대 경로 정규화
            const abs = new URL(pageUrl, location.href);
            const targetFull = abs.pathname + abs.search + abs.hash;

            console.log(`[MF] location  : ${current}`)
            console.log(`[MF] targetFull: ${targetFull}`)

            if (current === targetFull)
                return;

            // webNavigation start (React Router로 페이지 이동 시 pushstate가 발생함.)
            startMaxyPage(pageUrl, pageStartTime);
        }
        // 원래의 history 메서드 실행
        original.call(this, data, unused, url);
    };
}

/**
 * 페이지 이동 추적을 초기화합니다.
 */
/**
 * 페이지 이동(pushState/replaceState) 및 일반 페이지에서의 네비게이션을 추적하도록 초기화합니다.
 * SPA 여부에 따라 동작이 달라집니다.
 * @returns void
 */
export function initRouterCollector() {
    if (gIsSPA) {
        // popState는 event.ts에서 처리
        // pushState / replaceState 메서드 패치
        patchHistoryMethod('pushState');
        //patchHistoryMethod('replaceState');
    } else {
        /* 일반 페이지는 DOMContentLoaded, load Event에서 처리 */
    }
}

/**
 * 페이지 시작(total-log) 이벤트를 전송하고, 이전 페이지 로그를 전송한 후 현재 페이지 정보를 저장합니다.
 * @param pageUrl 페이지 URL (상대/절대)
 * @param logTm 로그 타임스탬프(ms)
 */
export async function startMaxyPage (pageUrl: string, logTm: number) {
    if (pageUrl == null || pageUrl == '') return

    // page id 를 생성한다.
    await createPageId();

    if (pageUrl.indexOf("http") < 0) {
        pageUrl = location.protocol + "//" + location.host + pageUrl
    }
    // 페이지시작 로그(total-log) 전송
    send('network', makeSendData({
        json: {
            url: pageUrl,
            value: ""
        },
        logType: maxyLogType.T_WebNav_Start.hexCode,
        iTime: 0,
        logTm: logTm,
        mapmStr: ""
    }));
    // 이전 페이지로그(page-log) 전송.
    await pageInfo.sendLog(logTm).then(()=> {
        // 현재 페이지로그 저장
        pageInfo.setStart(gPageId, pageUrl, logTm);
    });
}

export function endMaxyPage(pageUrl: string) {
    const perfNow = Math.floor(performance.timeOrigin + performance.now());
    if (pageUrl == null || pageUrl == '') return

    if (pageUrl.indexOf("http") < 0) {
        pageUrl = location.protocol + "//" + location.host + pageUrl
    }
    // pageinfo용으로도end시간 기재
    const intervaltime = pageInfo.setEnd(perfNow);

    // 페이지종료 로그(total-log) 전송
    send('network', makeSendData({
        json: {
            url: pageUrl,
            value: ""
        },
        logType: maxyLogType.T_WebNav_Finish.hexCode,
        iTime: intervaltime,
        logTm: perfNow,
        mapmStr: ""
    }));
}

type pageObj = {
    id: string,          // page ID
    nm: string,          // page name
    pstm: number|0,     // page start time
    petm: number|0,     // page end time
    ldgtm: number|0,    // page loading time
    vt: {               // web vital
        cls:number|0,
        fcp:number|0,
        inp:number|0,
        lcp:number|0,
        ttfb:number|0
    },
    perfm: string|"N",
    pre: {
        nm: string,      // pre page name
        pstm: number|0  // pre page start time
    }
};


class PageInfo {
    public pgKEY: string = KEY_MAXY_PGIF;
    public pgAry: any[] = [];

    public setPage(data: any[]) {
        sessionStorage.setItem(this.pgKEY, JSON.stringify(data));
    }

    public getPage(): any[] {
        let raw = sessionStorage.getItem(this.pgKEY);
        if (!raw) return [];
        try {
            let parsed = JSON.parse(raw);
            if (typeof parsed === "string") {
                parsed = JSON.parse(parsed);
            }
            return parsed;
        } catch {
            return [];
        }
    }

    public setStart(pageId: string, url: string, startTime: number) {

        // console.log(`setStartPage-${url},${startTime}`)

        this.pgAry = this.getPage() || [];
        let page: pageObj = {
            id: pageId,
            nm: url,
            pstm: startTime,
            petm: 0,
            ldgtm: 0,
            vt: { cls: 0, fcp: 0, inp: 0, lcp: 0, ttfb: 0 },
            perfm: "N",
            pre: {
                nm: gPrevPageData.nm,
                pstm: gPrevPageData.pstm
            }
        };
        this.pgAry.push(page);
        this.setPage(this.pgAry);
    }

    public setEnd(endTime: number): number {
        let intervaltime = 0;
        this.pgAry = this.getPage();
        const lastIdx = this.pgAry.length - 1;
        if (lastIdx >= 0) {
            this.pgAry[lastIdx].petm = endTime;
            if (this.pgAry[lastIdx].ldgtm === 0) {
                intervaltime = endTime - this.pgAry[lastIdx].pstm;
                this.pgAry[lastIdx].ldgtm = intervaltime;
            } else
                intervaltime = this.pgAry[lastIdx].ldgtm;
            this.setPage(this.pgAry);
        }
        return intervaltime;
    }

    public setLoadingTime(loadingTime: number) {
        this.pgAry = this.getPage();
        const lastIdx = this.pgAry.length - 1;
        if (lastIdx >= 0) {
            this.pgAry[lastIdx].ldgtm = loadingTime;
            this.setPage(this.pgAry);
        }
    }

    public async sendLog(startTime: number) {
        this.pgAry = this.getPage();
        if (Array.isArray(this.pgAry) && this.pgAry.length === 0) {
            console.log("sendLog(): Not exist page info.");
            setPageFlowOrder(1);
            this.pgAry = [];
            return;
        }

        if (!gEndPoint) return;

        const page: pageObj = this.pgAry.shift();
        const pageData = this.makeSendData(startTime, page);
        send('navigation', pageData);
    }

    private makeSendData(startTime: number, page: pageObj) {
        try {
            // 이전페이지 정보 임시저장
            setPrevPageData({
                nm: page.nm,
                pstm: page.pstm
            });
            const flowOrder = getPageFlowOrder() + 1;
            setPageFlowOrder(flowOrder);

            const errorCnt = getPageErrCnt();
            setPageErrCnt(0); // 다음페이지부터 다시 시작

            const logCnt = getPageLogCnt();
            setPageLogCnt(1); // 다음페이지부터 다시 시작

            const evtCnt = getPageEvtCnt();
            setPageEvtCnt(0); // 다음페이지부터 다시 시작.

            const reqTotal = getPageReqTotal();
            setPageReqTotal(0);

            const reqCnt = getPageReqCnt();
            setPageReqCnt(0);

            let comType = getComType();
            let pageMsg = [
                getOrCreateSessionUUID('UUID'),				// 0, deviceId
                gAppId,								        		// 1, packageNm
                gServerType,								    	// 2, serverType	- 개발: 0 / QA: 1 / 운영: 2
                gDeviceInfo.os.replace(/,/g, '^'),	// 3, osType
                '', 	                                            // 4, appVer
                gDeviceInfo.osVersion.replace(/,/g, '^'),  // 5, osVer - browserVer
                "",													// 6, appBuildNum
                gDeviceInfo.browser?.replace(/,/g, '^') ?? '', // 7, deviceModel - browser명
                "",													// 8, simOperatorNm 통신사업자명
                comType,									        // 9, comType
                Intl.DateTimeFormat().resolvedOptions().timeZone,	// 10, timezone
                "N",												// 11, vipYn
                getLoginYn(), 						                // 12, loginYn
                page.nm.replace(/,/g, '^'), // 13, reqUrl
                flowOrder,										    // 14, flowOrder
                page.pstm,											// 15, pageStartTm
                page.petm,      									// 16, pageEndTm
                getParentLogDate(),							        // 17, parentLogDate
                0,								                    // 18, eventIntervaltime
                (startTime - page.pstm) > 0 ? (startTime - page.pstm) : 0,   // 19, intervaltime, pageStartTm 부터 pageEndTm 까지의 시간
                page.ldgtm,									        // 20, loadingTime, webNav Start 부터 web Nav End 까지의 시간의 합
                reqTotal,   					// 21, responseTime
                reqCnt,                         // 22, requestCount
                evtCnt,							// 23, eventCount
                errorCnt,					    // 24, errorCount
                errorCnt,                       // 25, jsErrorCount
                0,								// 26, crashCount
                logCnt,							// 27, logCount
                0,								// 28, avgBatteryLvl
                0,								// 29, avgComSensitivity
                0,								// 30, avgCpuUsage
                0,								// 31, avgMemUsage
                0,								// 32, avgStorageUsage
                0,								// 33, maxBatteryLvl
                0,								// 34, maxComSensitivity
                0,								// 35, maxCpuUsage
                0,								// 36, maxMemUsage
                0,								// 37, maxStorageUsage
                0,								// 38, minBatteryLvl
                0,								// 39, minComSensitivity
                0,								// 40, minCpuUsage
                0,								// 41, minMemUsage
                0,								// 42, minStorageUsage
                0,								// 43, sumBatteryLvl
                0,								// 44, sumComSensitivity
                0,								// 45, sumCpuUsage
                0,								// 46, sumMemUsage
                0,							    // 47, sumStorageUsage
                maxyLogType.T_WebNav_Start.intCode, // 48, logType
                page.perfm,                            // 49, wtfFlag
                page.pre.nm.replace(/,/g, '^'),                    // 50, prePageUrl
                page.pre.pstm,                  // 51, preUrlTime
                (gAppInfoData.info === 'Y') ? getUserIdInMaxy() : '',						   // 52, info - 고객정보(userId)
                page.vt.cls,			    	// 53, CLS
                page.vt.inp,					// 54, INP
                page.vt.lcp,					// 55, LCP
                page.vt.fcp,					// 56, FCP
                page.vt.ttfb,					// 57, TTFB
                page.id,                        // 58, PageId
                "",                             // 59, Optional
                "",                             // 60, IP
                gDeviceInfo.browserVersion?.replace(/,/g, '^') ?? '',          // 61, Webview Version
                getOrCreateSessionUUID('SSID'), // 62, sessionIds
                `${window.innerWidth}x${window.innerHeight}x${window.devicePixelRatio}`, // 63, Webview 해상도, (Browser screen size)
            ].join(",");

            this.setPage(this.pgAry);

            //console.log("pageInfo:makeSendData- {}", pageMsg);

            //return `logTm=${startTime}&deviceId=${encodeURIComponent(getWebDeviceId())}&serverType=${gServerType}&appId=${encodeURIComponent(gAppId)}&p=${encodeURIComponent(pageMsg)}`;
            return pageMsg;
        } catch (err) {
            console.error(`${(err as Error).message}`);
        }
    }
}

export const pageInfo = new PageInfo();