import { gReplaceString } from "../globals";

const REPLACE_STRING = gReplaceString;

let replaceMap: Record<string, string> = {};

if (REPLACE_STRING) {
    replaceMap = REPLACE_STRING.split(",").reduce((acc, pair) => {
        const [key, value] = pair.split(":");
        if (key && value) {
            acc[key] = value;
        }
        return acc;
    }, {} as Record<string, string>);
}

export function checkJson(str: string): boolean {
    try {
        JSON.parse(str);
        return true;
    } catch (e) {
        return false;
    }
}

export function isInternal(url: string): boolean {
    return url.includes('/maxy/');
}