import { startMaxyPage, endMaxyPage } from "./router"
import {setMaxyLoginComplete, setMaxyLogoutComplete, getLoginYn, setUserIdInMaxy} from "../utils/storage"
import {maxyLogType} from "../utils/logType";
import {makeSendData, send} from "../utils/sender";
import {maxyLogin, setMaxyInfo} from "./event";

// 실제 런타임에서 window에 값 주입
window.maxy = {
    /**
     * MAXY login<br>
     *
     * @param userId
     */
    login(userId: string) {
        setUserIdInMaxy(userId);
        setMaxyLoginComplete();
        setMaxyInfo(userId);
        maxyLogin(userId);
    },
    logout() {
        setMaxyLogoutComplete();
    },
    getLoginYn() {
        return getLoginYn();
    },
    /**
     * MAXY startMaxyPage<br>
     * Web Page 정보가 수집되지 않거나, 특정 Page 정보를 강제 발생 시 사용합니다.<br>
     * pageUrl : full url<br>
     *
     * @param pageUrl
     */
    startMaxyPage: function(pageUrl: string) {
        startMaxyPage(pageUrl, Math.floor(performance.timeOrigin + performance.now()));
    },
    /**
     * MAXY endMaxyPage<br>
     * Web Page 정보가 수집되지 않거나, 특정 Page 정보를 강제 발생 시 사용합니다.<br>
     * pageUrl : 입력 예) http://www.naver.com/mail/mailBox 가 url인 경우 /mail/mailBox 입력<br>
     *
     * @param pageUrl
     */
    endMaxyPage: function(pageUrl: string) {
        endMaxyPage(pageUrl);
    },
    /**
     * MAXY sendMaxyLog<br>
     * Hybrid App 에서 Custom tag로 로그 전송 시 사용합니다.<br>
     * key : common, error 로 셋팅<br>
     * url : Custom tag가 존재하는 web 화면 url 예) https://www.thinkm.co.kr/main/main.do <br>
     * params : Custom tag로 송신하는 json string <br>
     *          key - error 인 경우 params json string에 아래 요소 필수 <br>
     *                errorCode
     *                errorMessage
     *                예) {
     *                      "errorCode": "-1",
     *                      "errorMessage": "System error occured.",
     *                      ...
     *                    }
     * @param key
     * @param url
     * @param params
     */
    sendMaxyLog(key: string, url: string, params: string) { // custom tag
        if (params == null) {
            params = "";
        }

        if (key !== "error") { // common
            send('network', makeSendData({
                json: {
                    url: url,
                    value: params
                },
                logType: maxyLogType.T_Custom_MAXY_Tagging_Common.hexCode,
                iTime: 0,
                logTm: Math.floor(performance.timeOrigin+performance.now()),
                mapmStr: ""
            }));
        } else {
            send('error', makeSendData({
                json: {
                    url: url,
                    value: params
                },
                logType: maxyLogType.T_Custom_MAXY_Tagging_Error.hexCode,
                iTime: 0,
                logTm: Math.floor(performance.timeOrigin+performance.now()),
                mapmStr: ""
            }));
        }
    }

};
export const maxy = window.maxy;