import { DeviceInfo, setDeviceInfoGlobal} from "../globals";
import { getDeviceInfoData, setDeviceInfoData} from "./storage";


export function getDeviceInfo(uaString: string = navigator.userAgent): DeviceInfo {
    // 이미 값이 있으면 즉시 반환
    let devInfo: DeviceInfo|null = getDeviceInfoData();
    if (devInfo) {
        setDeviceInfoGlobal(devInfo);
        return devInfo;
    }

    const ua = uaString.toLowerCase();
    let os: string = "Unknown";
    let osVersion: string = "";
    let model: string = "";
    let browser: string;
    let browserVersion: string;

    // OS 판별
    if (/windows nt/i.test(ua)) {
        os = "Windows";
        const match = ua.match(/windows nt ([0-9.]+)/i);
        osVersion = match?.[1] ?? "";
    } else if (/android/i.test(ua)) {
        os = "Android";
        const match = ua.match(/android ([0-9.]+)/i);
        osVersion = match?.[1] ?? "";
        // Android 기기 모델 추출 (일부 UA에서만 가능)
        const modelMatch = ua.match(/; ([^;]*?) build/i);
        model = modelMatch?.[1] ?? "";
    } else if (/iphone|ipad|ipod/i.test(ua)) {
        os = "iOS";
        const match = ua.match(/os ([0-9_]+)/i);
        osVersion = match?.[1]?.replace(/_/g, ".") ?? "";
        model = /iphone/i.test(ua)
            ? "iPhone"
            : /ipad/i.test(ua)
                ? "iPad"
                : "iPod";
    } else if (/mac os x/i.test(ua)) {
        os = "macOS";
        const match = ua.match(/mac os x ([0-9_]+)/i);
        if (navigator.maxTouchPoints > 0) {
            os = "iPadOS";
        }
        osVersion = match?.[1]?.replace(/_/g, ".") ?? "";
    } else if (/linux/i.test(ua) ) {
        os = "Linux";
    }

    // 브라우저 판별
    // brave, vivaldi 등은 useragent에 chorome을 사용.
    if (/whale/i.test(ua)) {
        browser = "Whale";
        browserVersion = ua.match(/whale\/([0-9.]+)/i)?.[1] ?? "";
    } else if (/opr\//i.test(ua) || /opera/i.test(ua)) {
        browser = "Opera";
        // Chromium 기반 Opera
        browserVersion =
            ua.match(/opr\/([0-9.]+)/i)?.[1]
            // 구형 Opera(Presto)
            ?? ua.match(/version\/([0-9.]+)/i)?.[1]
            ?? ua.match(/opera\/([0-9.]+)/i)?.[1]
            ?? "";
    } else if (/samsungbrowser/i.test(ua)) {
        browser = "SamsungBrowser";
        browserVersion = ua.match(/samsungbrowser\/([0-9.]+)/i)?.[1] ?? "";
    } else if (/chrome|crios/i.test(ua) && !/edge|edg/i.test(ua) && !/whale/i.test(ua)) {
        browser = "Chrome";
        browserVersion = ua.match(/(?:chrome|crios)\/([0-9.]+)/i)?.[1] ?? "";
    } else if (/safari/i.test(ua) && !/chrome|crios|android/i.test(ua)) {
        browser = "Safari";
        browserVersion = ua.match(/version\/([0-9.]+)/i)?.[1] ?? "";
    } else if (/firefox|fxios/i.test(ua)) {
        browser = "Firefox";
        browserVersion = ua.match(/(?:firefox|fxios)\/([0-9.]+)/i)?.[1] ?? "";
    } else if (/edg/i.test(ua)) {
        browser = "Edge";
        browserVersion = ua.match(/edg\/([0-9.]+)/i)?.[1] ?? "";
    } else {
        browser = "Unknown";
        browserVersion = "Unknown";
    }

    if (!(/android/i.test(ua)) && !(/iphone|ipad|ipod/i.test(ua))) {
        model = browser;
    }

    // WebView 판별 추가
    if (os === "iOS" || os === "iPadOS") {
        const isSafari = /safari/i.test(ua);
        const hasVersion = /version\//i.test(ua);
        // Safari 문자열이 없거나 Version/이 없으면 WebView 가능성 ↑
        if (!isSafari || !hasVersion) {
            browser = "Webview";
            // iOS WebView는 Safari 버전 대신 OS 버전과 WebKit 버전을 참고
            browserVersion = ua.match(/applewebkit\/([0-9.]+)/i)?.[1] ?? '';
        }
    } else if (os === "Android") {
        // Android WebView는 "; wv" 또는 "version/" 패턴이 특징
        if (ua.includes("; wv") || /version\/[\d.]+/i.test(ua)) {
            browser = "Webview";
            browserVersion = ua.match(/version\/([\d.]+)/i)?.[1] ?? browserVersion;
        }
    }

    devInfo = {os, osVersion, model, browser, browserVersion};

    setDeviceInfoGlobal(devInfo); // set global variable
    setDeviceInfoData(devInfo);   // save session storage

    return devInfo;
}

export function getComType() {
    let comType = 'n/a';
    const { connection } = (navigator as Navigator & { connection?: any });
    if (connection) {
        const eff = (connection.effectiveType || '').toString().toUpperCase();
        switch(eff) {
            case 'WIFI':
                comType = '1'
                break
            case '2G':
            case 'SLOW-2G':
                comType = '2'
                break
            case '3G':
                comType = '3'
                break
            case 'LTE':
            case '4G':
                comType = '4'
                break
            case '5G':
                comType = '5'
                break
        }
    }
    return comType;
}



