import './globals';
import './agent/error';
import './agent/event';
import './agent/maxy';
import './agent/network';
import './agent/perfm-obsv'
import './agent/session-play';
import './agent/router';
import './agent/web-vitals';
import './utils/device';
import './utils/hash';
import './utils/logType'
import './utils/sender';
import './utils/storage';
import './utils/storage';
import './utils/uuid';
import {initActionCollector, appStart } from "./agent/event";
import {initErrorCollector} from "./agent/error";
import {initNetworkCollector} from "./agent/network";
import {initSessionReplayCollector} from "./agent/session-play";
import {initRouterCollector} from "./agent/router";
import {initWebVitalsCollector} from "./agent/web-vitals";
import {scheduleSender, fetchWithTimeout} from "./utils/sender";
import {setAppInfoData, getAppInfoData, getStartedApp } from "./utils/storage";
import {createPageId, getOrCreateSessionUUID} from "./utils/uuid";
import {initPerfmObsvCollector} from "./agent/perfm-obsv";
import {
    gAppId, gEndPoint, gServerType, gDefPagePeriod, gDefLogUnit, gDefLogPeriod,
    AppInfoDto, setAppInfoGlobal
} from "./globals";
import {getDeviceInfo} from "./utils/device";

const dummyData: AppInfoDto = {
    ex: [],
    monitoring: 'Y',
    zip: 'N',
    fullMsg: 'N',
    info: 'N',
    logUnit: gDefLogUnit,
    logPeriod: gDefLogPeriod,
    pagePeriod: gDefPagePeriod
};

async function targetAppCheck() {

     try {
         const response = await fetchWithTimeout(gEndPoint.replace("/w", "/chk"), {
             method: "POST",
             headers: {
                 "Content-Type": "application/json",
             },
             body: JSON.stringify({
                 appId: encodeURIComponent(gAppId),
                 serverType: gServerType
             })
         });

         if (!response.ok) {
             console.log(`[MF] request failed with status ${response.status}`);
             return undefined;
         }

         const data: AppInfoDto = await response.json();
         if (!data) {
             console.log('[MF] /chk data not exist')
             return dummyData;
         }
         data.logUnit = data.logUnit ?? gDefLogUnit;
         data.logPeriod ? data.logPeriod *= 1000 : gDefLogPeriod;
         data.pagePeriod ? data.pagePeriod *= (1000 * 60) : gDefPagePeriod;
         return data;
     } catch (err) {
         console.log(`[MF] Error: ${(err as Error).message}. targetAppCheck is failed.`);
         return undefined;
     }
}

(async function startTracking() {
    createPageId(); // page id 를 생성한다.
    getDeviceInfo();
    if (!getStartedApp()) {
        const data =  await targetAppCheck();
        if (!data) {
            console.log("[MF] targetAppCheck is failed. maxy is stop");
            return;
        }
        if ("monitoring" in data && 'N' === data.monitoring) {
            console.log(`[MF] monitoring is ${data.monitoring}, targetAppCheck is failed. maxy is stop`);
            return;
        }
        setAppInfoGlobal(data); // set global variable
        setAppInfoData(data);   // save session storage
        getOrCreateSessionUUID('SSID'); // session id를 생성한다.
        appStart();
    } else {
        const appInfo: AppInfoDto|null = getAppInfoData();
        if (!appInfo) {
            console.log("[MF] Chk data load failed. maxy is stop");
            return;
        }
        setAppInfoGlobal(appInfo);
    }
    initSessionReplayCollector();
    initRouterCollector();
    initActionCollector();
    initPerfmObsvCollector();
    initWebVitalsCollector();
    initErrorCollector();
    initNetworkCollector();
    scheduleSender();
})();
