import {send, makeSendData } from '../utils/sender';
import {maxyLogType} from "../utils/logType"
import {gAppInfoData} from "../globals"
import {isInternal} from "../utils/string";
import {
    getPageEvtCnt,
    getPageReqCnt,
    getPageReqTotal,
    setPageEvtCnt,
    setPageReqCnt,
    setPageReqTotal
} from "../utils/storage";



export function initNetworkCollector() {
    hookingXHR();
    hookingFetch();
    hookingSubmit();
}

function hookingSubmit() {
    window.addEventListener("submit", (e: Event) => {
        const frm = e.target;
        if (frm instanceof HTMLFormElement) {
            const fields: Array<{ name: string; value: string; type: string }> = [];

            for (let i = 0; i < frm.elements.length; i++) {
                const el = frm.elements[i] as
                    | HTMLInputElement
                    | HTMLSelectElement
                    | HTMLTextAreaElement;

                if (el.name) {
                    // 비밀번호 등 민감 데이터는 마스킹
                    const safeValue = el.type === "password" ? "********" : el.value;

                    fields.push({
                        name: el.name,
                        value: safeValue,
                        type: el.type || el.tagName.toLowerCase(),
                    });
                }
            }

            send("network", makeSendData({
                json: {
                    url: frm.action,
                    netInfo: {
                        reqMsg: JSON.stringify(fields)
                    }
                },
                logType: maxyLogType.T_XMLHttpRequest_Submit.hexCode,
                iTime: 0,
                logTm: performance.timeOrigin + e.timeStamp,
                mapmStr: ""
            }));
            setPageEvtCnt(getPageEvtCnt() + 1);
        }
    },true);
}

function hookingFetch() {
    // FETCH 후킹
    type MaxyMeta = {
        method: string | null,
        url: string | null,
        openTime: number,
        requestStart: number,
        responseStart: number,
        responseEnd: number
    }

    type FetchType = typeof fetch;
    const originalFetch: FetchType = window.fetch;
    window.fetch = async (input: RequestInfo | URL, init?: RequestInit): Promise<Response> => {
        let maxyMeta: MaxyMeta = {
            method: null,
            url: null,
            openTime: 0,
            requestStart: 0,
            responseStart: 0,
            responseEnd: 0
        };
        maxyMeta.method = init?.method || 'GET';
        maxyMeta.url = typeof input === 'string'
                    ? input
                    : input instanceof Request
                        ? input.url
                        : input.toString(); // URL 객체 대응;
        maxyMeta.openTime = performance.now();

        if (isInternal(maxyMeta.url)) {
            return originalFetch(input, init);
        }

        const requestBody = init?.body;

        try {
            maxyMeta.requestStart = performance.now();
            send("network", makeSendData({
                json: {
                    url: maxyMeta.url,
                    netInfo: {
                        reqMsg: requestBody ?? ""
                    }
                },
                logType: maxyLogType.T_XMLHttpRequest_Request.hexCode,
                iTime: 0,
                logTm: performance.timeOrigin + performance.now(),
                mapmStr: ""
            }));

            const response = await originalFetch(input, init);

            maxyMeta.responseEnd = performance.now();

            const cloned = response.clone();
            const fullText = await cloned.text();
            const statusCode = cloned.status;

            let logtype = maxyLogType.T_XMLHttpRequest_Response.hexCode; // success
            if (!cloned.ok)
                logtype = maxyLogType.T_XMLHttpRequest_Error.hexCode;

            let waitTime = Math.round(maxyMeta.responseStart - maxyMeta.requestStart);
            let downloadTime = Math.round(maxyMeta.responseEnd - maxyMeta.responseStart);
            let totalTime = Math.round(maxyMeta.responseEnd - maxyMeta.requestStart);

            const diff = totalTime - (waitTime + downloadTime);
            if (Math.abs(diff) <= 1) {
                downloadTime += diff;
            } else if (Math.abs(diff) > 1) {
                console.warn("[MF] large diff", { waitTime, downloadTime, totalTime });
            }

            let resText = "";
            if (!isBinaryContentType(cloned.headers.get("Content-Type"))) {
                resText = extractLimitedResponseText(fullText);
            }

            const responseSize = getByteLength(fullText);
            const requestSize =  requestBody ? new TextEncoder().encode(String(requestBody)).length : 0;

            let apmData = "";
            try {
                apmData = cloned.headers.get("maxy") || "";
            } catch (e) {}

            send("network", makeSendData({
                json: {
                    url: maxyMeta.url,
                    value: resText,
                    interval: totalTime,
                    netInfo: {
                        statusCode: statusCode,
                        responseSize: responseSize,
                        requestSize: requestSize,
                        waitTime: waitTime,
                        downloadTime: downloadTime,
                        reqMsg: requestBody ?? "",
                    }
                },
                logType: logtype,
                iTime: totalTime,
                logTm: performance.timeOrigin + performance.now(),
                mapmStr: apmData
            }));

            if (logtype === maxyLogType.T_XMLHttpRequest_Response.hexCode) {
                setPageEvtCnt(getPageEvtCnt() + 1);
            }
            setPageReqTotal(getPageReqTotal() + totalTime);
            setPageReqCnt(getPageReqCnt() + 1);

            return response;
        } catch (error) {
            maxyMeta.responseEnd = performance.now();
            if (!maxyMeta.responseStart) {
                maxyMeta.responseStart = maxyMeta.requestStart;
            }

            send("error", makeSendData({
                json: error instanceof Error ? error.message : String(error),
                logType: maxyLogType.T_XMLHttpRequest_Exception.hexCode,
                iTime: Math.round(maxyMeta.responseEnd - maxyMeta.responseStart),
                logTm: performance.timeOrigin + performance.now(),
                mapmStr: ""
            }));

            throw error;
        }
    };
}


// XHR 후킹
declare global {
    interface XMLHttpRequest {
        _maxyMeta?: {
            method: string;
            url: string;
            requestStart: number;
            responseStart: number;
            responseEnd: number;
        };
        _realOpen?: typeof XMLHttpRequest.prototype.open;
        _realSend?: typeof XMLHttpRequest.prototype.send;
    }
}
function hookingXHR() {
    XMLHttpRequest.prototype._realOpen = XMLHttpRequest.prototype.open;
    XMLHttpRequest.prototype._realSend = XMLHttpRequest.prototype.send;

    XMLHttpRequest.prototype.open = function (
        this: XMLHttpRequest,
        method: string,
        url: string,
        async?: boolean,
        user?: string | null,
        password?: string | null
    ) {
        this._maxyMeta = {
            requestStart: 0, responseEnd: 0, responseStart: 0,
            method: method,
            url: url
        };
        const openTime = performance.now();

        // maxy 포함된 uri는 early return
        if (isInternal(url)) {
            this._realOpen!(method, url, async ?? true, user ?? null, password ?? null);
            return;
        }

        send("network", makeSendData({
            json: {
                url: this._maxyMeta.url,
                value: ""
            },
            logType: maxyLogType.T_XMLHttpRequest_Request.hexCode,
            iTime: 0,
            logTm: performance.timeOrigin + openTime,
            mapmStr: ""
        }));

        this._realOpen!(method, url, async ?? true, user ?? null, password ?? null);
    };

    // send
    XMLHttpRequest.prototype.send = function (this: XMLHttpRequest, body?: string | null) {
        const maxyMeta = this._maxyMeta;
        if (maxyMeta === undefined) {
            this._realSend!(body);
            return;
        }

        maxyMeta.requestStart = performance.now();

        // maxy 포함된 uri는 early return
        if (this._maxyMeta?.url && isInternal(this._maxyMeta.url)) {
            this._realSend!(body);
            return;
        }

        this.addEventListener("readystatechange", function () {
            if (this.readyState === 2) {
                maxyMeta.responseStart = performance.now();
            }
        });

        this.addEventListener("loadend", function () {
            maxyMeta.responseEnd = performance.now();

            // HEADERS_RECEIVED 가 발생하지 않았는 경우에 대한 보정
            if (maxyMeta.responseStart === 0) {
                maxyMeta.responseStart = maxyMeta.requestStart;
            }

            let waitTime = Math.round(maxyMeta.responseStart - maxyMeta.requestStart);
            let downloadTime = Math.round(maxyMeta.responseEnd - maxyMeta.responseStart);
            let totalTime = Math.round(maxyMeta.responseEnd - maxyMeta.requestStart);

            console.warn(`[MF] ${maxyMeta.requestStart}, ${maxyMeta.responseStart}, ${maxyMeta.responseEnd}`);
            console.warn("[MF] large diff", {  waitTime, downloadTime, totalTime });

            const diff = totalTime - (waitTime + downloadTime);
            if (Math.abs(diff) <= 1) {
                downloadTime += diff;
            } else if (Math.abs(diff) > 1) {
                console.warn("[MF] large diff", { waitTime, downloadTime, totalTime });
            }

            const fullText = (this as any).responseText;
            let resText = "";

            if (!isBinaryContentType(this.getResponseHeader("Content-Type"))) {
                resText = extractLimitedResponseText(fullText);
            }

            let statusCode = (this as any).status;
            const responseSize = getByteLength(fullText);
            const requestSize = getByteLength(body);

            let httpLogType: number;
            let logType: string = 'network';

            if (statusCode === null || statusCode === undefined || statusCode < 100) {
                statusCode = 0;
                resText = "Blocked request (e.g., ad blocker, CORS, CSP): " + (resText || "");
                httpLogType = maxyLogType.T_XMLHttpRequest_Exception.hexCode;
                waitTime = totalTime;
                downloadTime = 0;
                logType = 'error';
            } else if (statusCode >= 300) {
                httpLogType = maxyLogType.T_XMLHttpRequest_Error.hexCode;
                logType = 'error';
            } else {
                httpLogType = maxyLogType.T_XMLHttpRequest_Response.hexCode;
            }

            let apmData = "";
            try {
                apmData = this.getResponseHeader("maxy") || "";
            } catch (e) {}

           //parseCustomHeader(self);

            send(logType, makeSendData({
                json: {
                    url: maxyMeta.url,
                    value: resText,
                    interval: totalTime,
                    netInfo: {
                        statusCode: statusCode,
                        responseSize: responseSize,
                        requestSize: requestSize,
                        waitTime: waitTime,
                        downloadTime: downloadTime,
                        reqMsg: body ?? "",
                    }
                },
                logType: httpLogType,
                iTime: totalTime,
                logTm: performance.timeOrigin + performance.now(),
                mapmStr: apmData
            }));
            if (httpLogType === maxyLogType.T_XMLHttpRequest_Response.hexCode) {
                setPageEvtCnt(getPageEvtCnt() + 1);
            }
            setPageReqTotal(getPageReqTotal() + totalTime);
            setPageReqCnt(getPageReqCnt() + 1);
        });

        this._realSend!(body);
    };

}

function isBinaryContentType(contentType: string | null) {
    if (!contentType) return false;
    const mime = contentType.split(';')[0].trim().toLowerCase();
    return (
        mime === 'application/octet-stream' ||
        mime.startsWith('image/') ||
        mime.startsWith('audio/') ||
        mime.startsWith('video/') ||
        mime === 'application/pdf' ||
        mime === 'application/vnd.ms-excel' ||
        mime === 'application/vnd.openxmlformats-officedocument.wordprocessingml.document' ||
        mime.startsWith('application/zip') ||
        mime.startsWith('application/x-') // tar, 7z 등
    );
}

function extractLimitedResponseText(text: any) {
    // 기본 반환값은 전문 전체 또는 빈 문자열
    let resText = typeof text === 'string' ? text : '';
    if (gAppInfoData.fullMsg === 'N') {
        resText = '';
    }
    return resText;
}

function getByteLength(str: string | null | undefined) {
    if (typeof str !== 'string') return 0;
    if (typeof TextEncoder !== 'undefined') {
        return new TextEncoder().encode(str).length;
    }
    // fallback: 기존 방식
    let len = 0;
    for (let i = 0; i < str.length; i++) {
        const code = str.charCodeAt(i);
        if (code < 0x80) len += 1;
        else if (code < 0x800) len += 2;
        else if (0xD800 <= code && code <= 0xDBFF) {
            const next = str.charCodeAt(i + 1);
            if (0xDC00 <= next && next <= 0xDFFF) {
                len += 4;
                i++;
            } else {
                len += 3;
            }
        } else {
            len += 3;
        }
    }
    return len;
}