/**
 * Session Play Buffer Manager
 * 메모리 기반으로 세션 플레이 데이터를 관리하고 청크 단위로 전송하는 클래스
 */

interface SessionPlayItem {
    sessionId: string;
    timestamp: number;
    data: string;
    size: number;
}

interface ChunkSendResult {
    success: boolean;
    sentCount: number;
    error?: Error;
}

export class SessionPlayBuffer {
    private buffer: SessionPlayItem[] = [];
    private readonly maxBufferSize: number;
    private readonly maxChunkSize: number;
    private readonly maxItemCount: number;
    private currentBufferSize: number = 0;
    private isProcessing: boolean = false;
    private sendCallback: ((items: SessionPlayItem[]) => Promise<boolean>) | null = null;

    constructor(
        maxBufferSizeMB: number = 10,
        maxChunkSizeMB: number = 2,
        maxItemCount: number = 1000
    ) {
        this.maxBufferSize = maxBufferSizeMB * 1024 * 1024; // MB to bytes
        this.maxChunkSize = maxChunkSizeMB * 1024 * 1024;   // MB to bytes
        this.maxItemCount = maxItemCount;
    }

    /**
     * 세션 플레이 데이터 추가
     */
    async addData(sessionId: string, timestamp: number, data: string): Promise<void> {
        try {
            const dataSize = this.calculateStringSize(data);
            const item: SessionPlayItem = {
                sessionId,
                timestamp,
                data,
                size: dataSize
            };

            // 메모리 한계 체크
            if (this.shouldFlushBuffer(dataSize)) {
                await this.flushBuffer();
            }

            // 아이템 추가
            this.buffer.push(item);
            this.currentBufferSize += dataSize;

            // 버퍼 크기나 아이템 수가 임계값을 초과하면 자동 플러시
            if (this.shouldAutoFlush()) {
                setImmediate(() => this.flushBuffer());
            }

        } catch (error) {
            console.error('Failed to add session play data:', error);
            // 메모리 부족 등의 경우 버퍼 강제 플러시
            await this.emergencyFlush();
        }
    }

    /**
     * 버퍼의 모든 데이터를 청크 단위로 전송
     */
    async flushBuffer(): Promise<ChunkSendResult> {
        if (this.isProcessing || this.buffer.length === 0) {
            return { success: true, sentCount: 0 };
        }

        this.isProcessing = true;
        let totalSentCount = 0;

        try {
            const chunks = this.createChunks();

            for (const chunk of chunks) {
                const success = await this.sendChunk(chunk);

                if (success) {
                    totalSentCount += chunk.length;
                    this.removeItemsFromBuffer(chunk);
                } else {
                    // 전송 실패 시 해당 청크는 버퍼에 유지
                    console.warn(`Failed to send chunk with ${chunk.length} items`);
                    break;
                }
            }

            return { success: true, sentCount: totalSentCount };

        } catch (error) {
            console.error('Error during buffer flush:', error);
            return {
                success: false,
                sentCount: totalSentCount,
                error: error instanceof Error ? error : new Error('Unknown error')
            };
        } finally {
            this.isProcessing = false;
        }
    }

    /**
     * 긴급 상황에서 버퍼 정리 (메모리 부족 등)
     */
    private async emergencyFlush(): Promise<void> {
        try {
            console.warn('Emergency flush triggered - clearing buffer');

            // 가장 오래된 데이터부터 절반 제거
            const removeCount = Math.floor(this.buffer.length / 2);
            const removedItems = this.buffer.splice(0, removeCount);

            // 제거된 아이템들의 크기 계산하여 현재 버퍼 크기 업데이트
            const removedSize = removedItems.reduce((sum, item) => sum + item.size, 0);
            this.currentBufferSize = Math.max(0, this.currentBufferSize - removedSize);

        } catch (error) {
            console.error('Emergency flush failed:', error);
            // 최후의 수단으로 전체 버퍼 클리어
            this.clearBuffer();
        }
    }

    /**
     * 버퍼를 청크 단위로 분할
     */
    private createChunks(): SessionPlayItem[][] {
        const chunks: SessionPlayItem[][] = [];
        let currentChunk: SessionPlayItem[] = [];
        let currentChunkSize = 0;

        for (const item of this.buffer) {
            // 청크 크기 초과 시 새로운 청크 시작
            if (currentChunkSize + item.size > this.maxChunkSize && currentChunk.length > 0) {
                chunks.push([...currentChunk]);
                currentChunk = [];
                currentChunkSize = 0;
            }

            currentChunk.push(item);
            currentChunkSize += item.size;
        }

        // 마지막 청크 추가
        if (currentChunk.length > 0) {
            chunks.push(currentChunk);
        }

        return chunks;
    }

    /**
     * 청크 전송
     */
    private async sendChunk(chunk: SessionPlayItem[]): Promise<boolean> {
        if (!this.sendCallback) {
            console.error('Send callback not set');
            return false;
        }

        try {
            return await this.sendCallback(chunk);
        } catch (error) {
            console.error('Chunk send failed:', error);
            return false;
        }
    }

    /**
     * 전송 완료된 아이템들을 버퍼에서 제거
     */
    private removeItemsFromBuffer(sentItems: SessionPlayItem[]): void {
        try {
            const sentTimestamps = new Set(sentItems.map(item => item.timestamp));
            const removedSize = sentItems.reduce((sum, item) => sum + item.size, 0);

            this.buffer = this.buffer.filter(item => !sentTimestamps.has(item.timestamp));
            this.currentBufferSize = Math.max(0, this.currentBufferSize - removedSize);

        } catch (error) {
            console.error('Error removing items from buffer:', error);
            // 안전을 위해 버퍼 크기 재계산
            this.recalculateBufferSize();
        }
    }

    /**
     * 버퍼 플러시 필요 여부 확인
     */
    private shouldFlushBuffer(newItemSize: number): boolean {
        return (this.currentBufferSize + newItemSize) > this.maxBufferSize;
    }

    /**
     * 자동 플러시 필요 여부 확인
     */
    private shouldAutoFlush(): boolean {
        return this.buffer.length >= this.maxItemCount ||
            this.currentBufferSize >= this.maxChunkSize;
    }

    /**
     * 문자열 크기 계산 (UTF-8 기준)
     */
    private calculateStringSize(str: string): number {
        try {
            return new Blob([str]).size;
        } catch (error) {
            // Blob 생성 실패 시 대략적인 크기 계산
            return str.length * 2; // UTF-8 평균 2바이트로 추정
        }
    }

    /**
     * 버퍼 크기 재계산
     */
    private recalculateBufferSize(): void {
        try {
            this.currentBufferSize = this.buffer.reduce((sum, item) => sum + item.size, 0);
        } catch (error) {
            console.error('Error recalculating buffer size:', error);
            this.currentBufferSize = 0;
        }
    }

    /**
     * 버퍼 완전 정리
     */
    clearBuffer(): void {
        this.buffer = [];
        this.currentBufferSize = 0;
    }

    /**
     * 버퍼 상태 정보 반환
     */
    getBufferInfo(): {
        itemCount: number;
        bufferSizeMB: number;
        isProcessing: boolean;
    } {
        return {
            itemCount: this.buffer.length,
            bufferSizeMB: this.currentBufferSize / (1024 * 1024),
            isProcessing: this.isProcessing
        };
    }

    /**
     * 지정된 아이템들을 다른 버퍼로 안전하게 복사 (원본은 유지)
     */
    copyItemsTo(targetBuffer: SessionPlayBuffer, maxCount: number): SessionPlayItem[] {
        try {
            if (this.buffer.length === 0) {
                return [];
            }

            const itemsToCopy = this.buffer.slice(0, Math.min(maxCount, this.buffer.length));

            // 타겟 버퍼에 아이템들 추가
            for (const item of itemsToCopy) {
                targetBuffer.addItemDirectly(item);
            }

            return itemsToCopy;
        } catch (error) {
            console.error('Error copying items to target buffer:', error);
            return [];
        }
    }

    /**
     * 아이템을 직접 버퍼에 추가 (내부 사용용)
     */
    addItemDirectly(item: SessionPlayItem): void {
        this.buffer.push(item);
        this.currentBufferSize += item.size;
    }

    /**
     * 특정 타임스탬프의 아이템들을 제거 (전송 성공 후 정리용)
     */
    removeItemsByTimestamps(timestamps: number[]): void {
        try {
            const timestampSet = new Set(timestamps);
            const itemsToRemove = this.buffer.filter(item => timestampSet.has(item.timestamp));

            // 제거할 아이템들의 크기 계산
            const removedSize = itemsToRemove.reduce((sum, item) => sum + item.size, 0);

            // 버퍼에서 해당 아이템들 제거
            this.buffer = this.buffer.filter(item => !timestampSet.has(item.timestamp));
            this.currentBufferSize = Math.max(0, this.currentBufferSize - removedSize);

            //console.log(`Removed ${itemsToRemove.length} items from storage buffer`);
        } catch (error) {
            console.error('Error removing items by timestamps:', error);
        }
    }

    /**
     * 리소스 정리
     */
    destroy(): void {
        this.clearBuffer();
        this.sendCallback = null;
        this.isProcessing = false;
    }
}