import {makeSendData, send} from '../utils/sender';
import {maxyLogType} from "../utils/logType";
import {isInternal} from "../utils/string";
import {pageInfo} from "./router";

function mapping(obsvJson: Object) {
    /**
     * 미리 정의된 매핑 객체
     */
    let map = {
        "name": "1",
        "entryType": "2",
        "startTime": "3",
        "duration": "4",
        "initiatorType": "5",
        "deliveryType": "6",
        "nextHopProtocol": "7",
        "renderBlockingStatus": "8",
        "workerStart": "9",
        "redirectStart": "10",
        "redirectEnd": "11",
        "fetchStart": "12",
        "domainLookupStart": "13",
        "domainLookupEnd": "14",
        "connectStart": "15",
        "secureConnectionStart": "16",
        "connectEnd": "17",
        "requestStart": "18",
        "responseStart": "19",
        "firstInterimResponseStart": "20",
        "finalResponseHeadersStart": "21",
        "responseEnd": "22",
        "transferSize": "23",
        "encodedBodySize": "24",
        "decodedBodySize": "25",
        "responseStatus": "26",
        "serverTiming": "27",
        "attribution": "28",
        "renderStart": "29",
        "styleAndLayoutStart": "30",
        "firstUIEventTimestamp": "31",
        "blockingDuration": "32",
        "scripts": "33",
        "renderTime": "35",
        "loadTime": "36",
        "hadRecentInput": "40",
        "lastInputTime": "41",
        "sources": "42",
        "unloadEventStart": "43",
        "unloadEventEnd": "44",
        "domInteractive": "45",
        "domContentLoadedEventStart": "46",
        "domContentLoadedEventEnd": "47",
        "domComplete": "48",
        "loadEventStart": "49",
        "loadEventEnd": "50",
        "redirectCount": "52",
        "activationStart": "53",
        "criticalCHRestart": "54",
        "notRestoredReasons": "55",
        "interactionId": "56",
        "processingStart": "57",
        "processingEnd": "58",
        "cancelable": "59"
    }
    /**
     * JSON 문자열을 받아와 "key"만 치환하는 mapping 함수
     * @param jsonStr msg json 문자열
     * @returns {*}
     */
    if (obsvJson === null || obsvJson === undefined) return;

    let result = JSON.stringify(obsvJson);
    try {
        for (const [fullKey, shortKey] of Object.entries(map)) {
            const regex = new RegExp(`"${fullKey}"`, 'g');  // JSON key만 정확히 치환
            result = result.replace(regex, `"${shortKey}"`);
        }
    } catch (e) {
        console.error("Error while mapping performance keys:", e);
    }

    return JSON.parse(result);
}

export function initPerfmObsvCollector() {
    const obsvCallback: PerformanceObserverCallback = (list, obj) => {
        list.getEntries().forEach(entry => {
            let json
            let intervaltime = 0;

            switch (entry.entryType) {
                case 'resource':
                    const rescEntry = entry as PerformanceResourceTiming;
                    intervaltime = rescEntry.responseEnd - entry.startTime
                    json = entry.toJSON()
                    break
                case 'navigation':
                    const navEntry = entry as PerformanceNavigationTiming;
                    intervaltime = Math.round(navEntry.loadEventEnd - entry.startTime)
                    json = entry.toJSON()
                    break
                case 'first-input':
                    json = entry.toJSON()
                    break
                case 'largest-contentful-paint':
                    json = entry.toJSON()
                    break
                case 'event':
                    if (entry.name === 'click') json = entry.toJSON()
                    break
                case 'paint':
                    json = entry.toJSON()
                    break
                case 'longtask': // Long Task 감지
                    json = entry.toJSON()
                    break
                case 'layout-shift':
                    json = entry.toJSON()
                    break
                default:
                    json = entry.toJSON()
                    break
            }

            if (json === null || json === undefined)
                return;

            if (isInternal(json.name))
                return;

            json = mapping(json);

            if (intervaltime > 0) {
                pageInfo.setLoadingTime(intervaltime);
            }

            send('perfm-obsv', makeSendData({
                json: {
                    value: json
                },
                logType: maxyLogType.T_WebNav_Res_Down_Time_Start.hexCode,
                iTime: 0,
                logTm: Math.floor(performance.timeOrigin + entry.startTime),
                mapmStr: ''
            }));

            // pageInfo에 perfm정보추가
            let pgAry = pageInfo.getPage();
            if (pgAry && pgAry.length > 0) {
                const idx = pgAry.length-1;
                if ('Y' != pgAry[idx].perfm) {
                   pgAry[idx].perfm = "Y";
                   pageInfo.setPage(pgAry);
                }
            } else {
                console.warn('pageInfo.getPage() returned invalid data:', pgAry);
            }
        });

        "addEventListener" in performance && performance.addEventListener("resourcetimingbufferfull", function () {
            performance.clearResourceTimings();
        });
    }

    let entryType: string[] = [
        "resource",
        "navigation",
        "first-input",
        "largest-contentful-paint",
        "paint",
        "element",
        "layout-shift",
        "visibility-state",
        "long-animation-frame",
        "longtask",
        "taskattribution",
        "event"
    ]; //"mark", "measure",

    let entryTypeBrowser: string[] = [];
    entryType.forEach(function (item) {
        if (PerformanceObserver.supportedEntryTypes.includes(item)) {
            entryTypeBrowser.push(item);
        }
    });
    const obsv = new PerformanceObserver(obsvCallback);
    obsv.observe({entryTypes: entryTypeBrowser});
}