const M_KEY = `_mf_event_${gAppId}_${gServerType}`;
const P_KEY = `_mf_page_${gAppId}_${gServerType}`;
const S_KEY = `_mf_splay_${gAppId}_${gServerType}`;
const PING_KEY = `_mf_ping_${gAppId}_${gServerType}`;
let mBuffer: any[] = [];
let pBuffer: any[] = [];

// 세션 플레이 이중 버퍼
let storageBuffer: SessionPlayBuffer | null = null;  // 데이터 저장용 버퍼
let sendBuffer: SessionPlayBuffer | null = null;     // 데이터 전송용 버퍼

/**
 * fetch를 지정한 시간 내에 완료하지 못하면 타임아웃 에러를 발생시키는 래퍼입니다.
 * @param url 요청 URL
 * @param options fetch 옵션
 * @param timeout 타임아웃(ms) 기본 20초
 * @returns Response를 resolve하는 Promise
 */
export function fetchWithTimeout(url: string, options: RequestInit, timeout = 1000 * 20): Promise<Response> {
    return new Promise((resolve, reject) => {
        const id = setTimeout(() => reject(new Error("fetch timeout")), timeout);
        fetch(url, options).then(res => {
            clearTimeout(id);
            resolve(res);
        }).catch(err => {
            clearTimeout(id);
            reject(err);
        });
    });
}
async function makeDeflateAndBase64(data: string): Promise<string> {
    const compressed = pako.deflate(encoder.encode(data)); // 압축
    const base64 = await toBase64(compressed);
    return base64.replace(/\+/g, "-").replace(/\//g, "_").replace(/=+$/, "");
}

function saveData(key: string, data: any) {
    const saved = loadBackup(key);
    if (gAppInfoData.zip === 'Y') {
        makeDeflateAndBase64(data).then(value => {
            saved.push(value);
            saveBackup(key, saved);
        });
    } else {
        saved.push(data);
        saveBackup(key, saved);
    }
}

/**
 * 수집된 데이터를 로컬 백업에 저장하거나 세션플레이 버퍼로 전달합니다.
 * 실제 전송은 scheduleSender가 주기적으로 처리합니다.
 * @param type 'network' | 'error' | 'web-vital' | 'perfm-obsv' | 'navigation' | 'session-play'
 * @param data 전송 대상 원본 데이터
 */
export function send(type: string, data: any) {
    if (type === 'network' || type === 'error' || type === 'web-vital' || type === 'perfm-obsv') {  // log
        if (type === 'network') {
            setPageLogCnt(getPageLogCnt() + 1);
        } else if (type === 'error') {
            // 에러 카운트 증가
            setPageErrCnt(getPageErrCnt() + 1);
        }
        saveData(M_KEY, data);
    } else if (type === 'navigation') {       // page-log
        saveData(P_KEY, data);
    } else if (type === 'session-play') {    // session-play log
        handleSessionPlayData(data).then(() => {});
    }
}

function pushPromise(key: string, promises: Promise<any>[]) {
    let item: {
        appId: string;
        serverType: string;
        deviceId: string;
        sessionId?: string | Blob | null;
        timestamp?: number | Blob | null;
        m?: string | Blob | null;
        p?: string | Blob | null;
        s?: string | Blob | null;
    } = {
        appId: encodeURIComponent(gAppId),
        serverType: gServerType,
        deviceId: encodeURIComponent(getOrCreateSessionUUID('UUID')),
    };

    switch (key) {
        case M_KEY:
            mBuffer = loadBackupForSend(key);
            if (Array.isArray(mBuffer) && mBuffer.length === 0)
                return;
            item.m = mBuffer.map(obj => `${obj}`).join('\n');
            break;
        case P_KEY:
            pBuffer = loadBackupForSend(key);
            if (Array.isArray(pBuffer) && pBuffer.length === 0)
                return;
            item.p = pBuffer.map(obj => `${obj}`).join('\n');
            break;
        case S_KEY:
            const { data: sessionData, timestamps } = getSessionPlayDataFromStorageBuffer();
            if (!sessionData || sessionData.length === 0) {
                return;
            }
            // 전송할 데이터의 타임스탬프 저장 (전송 성공 시 정리용)
            pendingTransmissionTimestamps = timestamps;
            item.s = sessionData.map(obj => `${obj}`).join('\n');

            // 디버깅: 전송 데이터 크기 확인
            const dataSize = JSON.stringify(item).length;
            console.log(`[MF] ${(new Date(Date.now())).toLocaleString()}-S_KEY: Sending ${sessionData.length} items, total size: ${dataSize} bytes`);
            break;
        case PING_KEY:
            item.m = null;
            item.p = null;
            item.s = null;
            break;
    }

    //console.log("send: {}", JSON.stringify(item));
    //const promises: Promise<any>[] = [];

    let maxyHeader;
    if (gAppInfoData.zip === 'Y') {
        maxyHeader = {
            "Content-Type": "application/json",
            "Content-Encoding": "mz"
        };
    } else {
        maxyHeader = {
            "Content-Type": "application/json",
        };
    }
    promises.push(fetchWithTimeout(gEndPoint, {
        method: "POST",
        headers: maxyHeader,
        body: JSON.stringify(item)
    }));

    Promise.all(promises).then(results => {
        if (results.every(r => r.ok)) {
            if (key === M_KEY) mBuffer.splice(0, mBuffer.length);
            else if (key === P_KEY) pBuffer.splice(0, pBuffer.length);
            else if (key === S_KEY) {
                // 전송 성공 시 저장 버퍼에서 해당 데이터 제거
                clearSentSessionPlayDataFromStorageBuffer(pendingTransmissionTimestamps);
                pendingTransmissionTimestamps = [];
            }
        } else {
            results.forEach((r, i) => {
                if (!r.ok) {
                    console.log(`request ${i} failed with status ${r.status}`);
                }
            });
        }
        if (key === M_KEY || key === P_KEY)
            stopPingInterval(); // 통신이 성공하면 ping을 보내지 않도록 설정.
    }).catch(err => {
        console.log(err);
    });
}

/**
 * setInterval() 대신 setTimeout으로 대체 (setInterval은 모바일 등에서 정확한 동작을 보장하지 못함)
 * @param callback
 * @param delay
 */
function customIntervalAsync(callback: () => Promise<void> | void, delay: number): () => void {
    let stopped = false;

    async function loop() {
        await callback();
        if (!stopped) setTimeout(loop, delay);
    }
    setTimeout(loop, delay);
    return () => { stopped = true; };
}

export function scheduleSender() {
    // 이중 버퍼 시스템 초기화
    initializeSessionPlayBuffers();

    /*setInterval(() => {
        const promisesM: Promise<any>[] = [];
        pushPromise(M_KEY, promisesM);
    }, gAppInfoData?.logPeriod ?? 5*1000);*/

    const mItvFunc = customIntervalAsync(async () => {
        const promisesM: Promise<any>[] = [];
        pushPromise(M_KEY, promisesM);
    }, gAppInfoData?.logPeriod ?? 5*1000);
    setTotalIntervalStopFunc(mItvFunc);

    /*setInterval(() => {
        const promisesP: Promise<any>[] = [];
        pushPromise(P_KEY, promisesP);
    }, gTimeoutPageLog ?? 2*1000);*/

    const pItvFunc = customIntervalAsync(async () => {
        const promisesP: Promise<any>[] = [];
        pushPromise(P_KEY, promisesP);
    }, gTimeoutPageLog ?? 2*1000);
    setPageIntervalStopFunc(pItvFunc);

    /*setInterval(() => {
        const promisesS: Promise<any>[] = [];
        pushPromise(S_KEY, promisesS);
    }, gTimeoutSReplayLog ?? 4*1000);*/

    const sItvFunc = customIntervalAsync(async () => {
        const promisesS: Promise<any>[] = [];
        pushPromise(S_KEY, promisesS);
    }, gTimeoutSReplayLog ?? 4*1000);
    setSReplayIntervalStopFunc(sItvFunc);

    // 지정한 시간(MAXT_FT_CHK_PING_INTERVAL_TIME)마다 ping interval 상태 확인 → 꺼져 있으면 다시 켜기
    /*setInterval(() => {
        if (gPingIntervalId === null) {
            startPingInterval();
        }
    }, gChkPingItvTime ?? 30*1000);*/

    const chkPing = customIntervalAsync(async () => {
        if (gPingIntervalStopFunc === null) {
            startPingInterval();
        }
    }, gChkPingItvTime ?? 30*1000);
}

// 페이지를 떠나기 전 발생 (브라우저탭 닫기, 다른 페이지 이동, 새로고침)
export function onBeforeUnload(event: BeforeUnloadEvent) {
    if (storageBuffer) {
        storageBuffer.destroy();
    }
    if (sendBuffer) {
        sendBuffer.destroy();
    }

    //clearBackup(M_KEY);
    //clearBackup(P_KEY);
    //clearBackup(S_KEY);
}

// 인터벌을 시작하는 함수
function startPingInterval() {
    if (gPingIntervalStopFunc === null) {
        /*const id = window.setInterval(() => {
            const promisesPing: Promise<any>[] = [];
            pushPromise(PING_KEY, promisesPing);
        }, gTimeoutPingLog);
        setPingIntervalId(id);*/

        const tmpFunc = customIntervalAsync(async () => {
            const promisesPing: Promise<any>[] = [];
            pushPromise(PING_KEY, promisesPing);
        }, gTimeoutPingLog);
        setPingIntervalStopFunc(tmpFunc);

        console.log("start ping interval");
    }
}

// 인터벌을 멈추는 함수
function stopPingInterval() {
    if (gPingIntervalStopFunc !== null) {
        //clearInterval(gPingIntervalId);
        //setPingIntervalId(null);

        gPingIntervalStopFunc();
        setPingIntervalStopFunc(null);

        console.log("stop ping interval");
    }
}

function loadBackupForSend(key: string): any[] {
    try {
        const raw = localStorage.getItem(key);
        // return raw ? JSON.parse(raw) : []; // logUnit보다 적으면 보내지 않고, 많으면 전부 보낸다.

        if (raw) {
            let temp: any[] = JSON.parse(raw);
            if (temp.length > gAppInfoData.logUnit) { // logUnit보다 적으면 보내지 않고, 많으면 전부 보낸다.
                const res = temp.slice(0, gAppInfoData.logUnit);
                safeSetItemLocal(key, JSON.stringify(temp.slice(gAppInfoData.logUnit)));
                return res;
            } else {
                localStorage.removeItem(key);
                return temp;
            }
        } else {
            return [];
        }

    } catch {
        return [];
    }
}

function loadBackup(key: string): any[] {
    try {
        const raw = localStorage.getItem(key);
        return raw ? JSON.parse(raw) : [];
    } catch {
        return [];
    }
}

function saveBackup(key: string, data: any[]) {
    try {
        safeSetItemLocal(key, JSON.stringify(data));
    } catch (e) {
        console.error("localStorage save fail", e);
    }
}

function clearBackup(key: string) {
    localStorage.removeItem(key);
}

/**
 * 세션 플레이 데이터 처리 (이중 버퍼 시스템 사용)
 */
async function handleSessionPlayData(data: string): Promise<void> {
    try {
        if (!storageBuffer) {
            initializeSessionPlayBuffers();
        }

        // 데이터 파싱 (timestamp|compressedData 형태)
        const parts = data.split('|');
        if (parts.length !== 2) {
            console.error('Invalid session play data format');
            return;
        }

        const [timestampStr, compressedData] = parts;
        const timestamp = parseInt(timestampStr, 10);

        if (isNaN(timestamp)) {
            console.error('Invalid timestamp in session play data');
            return;
        }

        // 저장 버퍼에 데이터 추가
        await storageBuffer!.addData(getOrCreateSessionUUID('SSID'), timestamp, compressedData);

    } catch (error) {
        console.error('Failed to handle session play data:', error);
    }
}

/**
 * 저장 버퍼에서 전송 버퍼로 데이터 이동 후 전송용 데이터 반환
 */
function getSessionPlayDataFromStorageBuffer(): { data: string[], timestamps: number[] } {
    try {
        if (!storageBuffer || !sendBuffer) {
            return { data: [], timestamps: [] };
        }

        const storageInfo = storageBuffer.getBufferInfo();
        if (storageInfo.itemCount === 0) {
            return { data: [], timestamps: [] };
        }

        // 저장 버퍼에서 전송 버퍼로 모든 데이터 복사
        const copiedItems = storageBuffer.copyItemsTo(sendBuffer, storageInfo.itemCount);

        if (!copiedItems || copiedItems.length === 0) {
            return { data: [], timestamps: [] };
        }

        //console.log(`Copied ${copiedItems.length} items from storage buffer to send buffer`);

        // 전송용 데이터와 타임스탬프 배열 생성
        const data = copiedItems.map(item =>
            `${item.sessionId}|${item.timestamp}|${item.data}`
        );
        const timestamps = copiedItems.map(item => item.timestamp);

        return { data, timestamps };

    } catch (error) {
        console.error('Failed to get session play data from storage buffer:', error);
        return { data: [], timestamps: [] };
    }
}

/**
 * 전송 완료된 세션 플레이 데이터를 저장 버퍼에서 제거
 */
function clearSentSessionPlayDataFromStorageBuffer(timestamps: number[]): void {
    try {
        if (!storageBuffer || !sendBuffer) {
            return;
        }

        // 저장 버퍼에서 전송 완료된 데이터 제거
        storageBuffer.removeItemsByTimestamps(timestamps);

        // 전송 버퍼 정리
        sendBuffer.clearBuffer();

        //console.log(`Cleared ${timestamps.length} items from storage buffer after successful transmission`);
    } catch (error) {
        console.error('Failed to clear sent session play data from storage buffer:', error);
    }
}

// 전송 중인 데이터의 타임스탬프를 저장 (전송 성공 시 정리용)
let pendingTransmissionTimestamps: number[] = [];

/**
 * 세션 플레이 이중 버퍼 시스템 초기화
 */
function initializeSessionPlayBuffers(): void {
    try {
        // 저장용 버퍼 (더 큰 용량)
        storageBuffer = new SessionPlayBuffer(
            20,    // 최대 버퍼 크기: 20MB
            5,     // 최대 청크 크기: 5MB (사용하지 않음)
            1000   // 최대 아이템 수: 1000개
        );

        // 전송용 버퍼 (작은 용량, 임시 사용)
        sendBuffer = new SessionPlayBuffer(
            20,     // 최대 버퍼 크기: 20MB
            5,      // 최대 청크 크기: 5MB (사용하지 않음)
            1000    // 최대 아이템 수: 1000개
        );
    } catch (error) {
        console.error('Failed to initialize session play buffers:', error);
    }
}


export function makeSendData({json, logType, iTime, logTm, mapmStr}: {
    json: Object,
    logType: number,
    iTime: number,
    logTm: number,
    mapmStr: string
}) {
    try {
        let intervalTime = Number((iTime)).toFixed(0);
        let url: any = document.location.href;
        if (typeof json === 'object' && "url" in json) {
            url = json.url;
        }

        if (logType === maxyLogType.T_XMLHttpRequest_Send.hexCode || logType === maxyLogType.T_WebNav_Res_Down_Time_Start.hexCode) {
            if (typeof json === 'object' && "name" in json)
                url = json.name;
        }

        let maxyLogTm = (logTm == null) ? new Date().getTime() : logTm;

        if (mapmStr === undefined || mapmStr === null)
            mapmStr = '';
        else
            mapmStr = (mapmStr).replace(/,/g, '^');

        let lMsg: any[];
        lMsg = [
            maxyLogTm,                                                          // 0, logTm 로그발생시간 timestamp
            getOrCreateSessionUUID('UUID'),                                     // 1, deviceId
            logType,                                                            // 2, logType
            getComType(),                                                       // 3, 통신유형
            gDeviceInfo.osVersion?.replace(/,/g, '^') ?? '',    // 4, OS버전
            '',                                                                 // 5, App Version
            gDeviceInfo.browser?.replace(/,/g, '^') ?? '',       // 6, Model -- browser명
            0,                                                                  // 7, Memory Usage
            0,                                                                  // 8, CPU Usage
            -1,                                                                 // 9, 통신감도 빈값으로 2024.01.08
            0,                                                                  // 10, Battery Level
            url?.replace(/,/g, '^'),                                             // 11, Request URL
            (json as any).value !== '' ? (JSON.stringify((json as any).value) ?? "").replace(/,/g, '^') : '', // 12, Response Message
            intervalTime,                                                       // 13, IntervalTime
            0,                                                                  // 14, Storage Usage
            0,                                                                  // 15, Total Storage
            Intl.DateTimeFormat().resolvedOptions().timeZone,                   // 16, Time Zone
            gDeviceInfo.browserVersion?.replace(/,/g, '^') ?? '', // 17, Webview Version
            '',                                                                 // 18, App Build Version
            document.referrer?.replace(/,/g, '^'),    // 19, Referer
            '',                                                                 // 20, IP
            '',                                                                 // 21, 통신사
            getLoginYn(),                                                       // 22, 로그인 여부
            getOrCreateSessionUUID('SSID'),                              // 23, MAXY 접속 세션 ID
            'Y',                                                                // 24, 웹 여부
            gAppId,                                                             // 25, App ID
            gServerType,                                                        // 26, Server Type
            gDeviceInfo.os?.replace(/,/g, '^') ?? '',           // 27, OS유형
            '',                                                                 // 28, 딥 링크 스킴 정보
            sessionStorage.getItem("currentPageUrl"),                     // 29, 해당 로그가 실행된 페이지URL
            mapmStr,                                                            // 30. Jennifer 연계정보
            (gAppInfoData.info === 'Y') ? getUserIdInMaxy() : '',               // 31, 로그인 정보 전송 2025.03.27 -> 2025.08.21 UserID 로 변경
            '',                                                                 // 32, Optional
            navigator.language,                                                 // 33, OS 언어 (Browser언어설정)
            `${window.innerWidth}x${window.innerHeight}x${window.devicePixelRatio}`, // 34, Webview 해상도, (Browser screen size)
            (json as any).netInfo?.statusCode ?? 0,                                 // 35, Network Status code
            (json as any).netInfo?.responseSize ?? 0,                       // 36, Network Response byte size
            (json as any).netInfo?.requestSize ?? 0,                        // 37, Network Request byte size
            (json as any).netInfo?.waitTime ?? 0,                               // 38, Network Wait time
            (json as any).netInfo?.downloadTime ?? 0,                           // 39, Network Download time
            ((json as any).netInfo?.reqMsg ?? "").replace(/,/g, '^'),     // 40, Network Request Message
            gPageId                                                             // 41, Page ID
        ];

        //console.log("sender:makeSendData/{} - {}", logType, lMsg.join(','));
        //console.log(lMsg.map((obj,idx) => ({...obj})));

        return lMsg.join(',');
        //return lMsg.map(obj => obj.val).join(',');
    } catch (err) {
        console.error(`${(err as Error).message}`);
    }
}

