import {setPageId, gUuidStg, gAppId, gServerType} from "../globals";
import { getCookie, setCookie } from "./storage";

const KEY_MAXY_UUID = `mf-front-uuid-${gAppId}-${gServerType}`;
const KEY_MAXY_SSID = `mf-front-ssid-${gAppId}-${gServerType}`;

/**
 * create id (type = UUID, SSID)
 */
export function getOrCreateSessionUUID(type: string): string {
    let myid: string | null = null;
    let myKey: string = KEY_MAXY_UUID;
    if (type === 'UUID') {
        if (gUuidStg === 'sstg') {
            myid = sessionStorage.getItem(myKey);
        } else if (gUuidStg === 'cookie') {
            myid = getCookie(myKey);
        }
    } else if (type === 'SSID') {
        myKey = KEY_MAXY_SSID;
        myid = sessionStorage.getItem(myKey);
    }

    if (!myid) {
        try {
            myid = crypto.randomUUID();
        } catch (e) {
            console.warn("[MF] crypto.randomUUID not supported");
            // fallback UUID 생성 (RFC4122 형식 간단 구현)
            myid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, c => {
                const r = Math.random() * 16 | 0;
                const v = c === 'x' ? r : (r & 0x3 | 0x8);
                return v.toString(16);
            });
        }

        if (type === 'UUID') {
            if (gUuidStg === 'sstg') {
                sessionStorage.setItem(myKey, myid);
            } else if (gUuidStg === 'cookie') {
                setCookie(myKey, myid, 30);
            }
        } else if (type === 'SSID') {
            sessionStorage.setItem(myKey, myid);
        }
    }

    return myid;
}

// page 정보에 넘겨줄 page id를 만든다.
export async function createPageId()  {
    setPageId(getOrCreateSessionUUID('UUID') + new Date().getTime());
}