import {onCLS, onFCP, onINP, onLCP, onTTFB} from 'web-vitals';
import {makeSendData, send} from '../utils/sender';
import {maxyLogType} from "../utils/logType";
import { pageInfo } from "./router"


type JsonValue = string | null | JsonObject | JsonArray;
type MetricObject = { name?: string; value?: number; id?: string };
interface JsonObject { [key: string]: JsonValue }
interface JsonArray extends Array<JsonValue> {}

type WeakKey = object;

function normalizeEntry(
    entry: JsonValue,
    seen: WeakSet<WeakKey> = new WeakSet(),
    depth: number = 0,
    maxDepth: number = 20
): JsonValue {
    if (depth > maxDepth) return '[MaxDepth]';

    if (entry === null || typeof entry !== 'object')
        return null;

    if (seen.has(entry))
        return '[Circular]';

    seen.add(entry);

    if (entry instanceof Date)
        return entry;

    if (typeof (entry as any).toJSON === 'function') {
        try {
            const jsonified = (entry as any).toJSON();
            return normalizeEntry(jsonified, seen, depth + 1, maxDepth);
        } catch {
            return '[toJSON Error]';
        }
    }

    const normalized: JsonObject | JsonArray = Array.isArray(entry) ? [] : {};

    for (const key in entry) {
        if (!Object.prototype.hasOwnProperty.call(entry, key))
            continue;

        try {
            const value = (entry as any)[key];

            if (typeof value === 'number') {
                // CLS와 같은 값은 소수점 4자리 고정
                (normalized as any)[key] = Number(value.toFixed(4));
            } else if (typeof value === 'object' && value !== null) {
                (normalized as any)[key] = normalizeEntry(value, seen, depth + 1, maxDepth);
            } else {
                (normalized as any)[key] = value;
            }
        } catch {
            (normalized as any)[key] = '[Error]';
        }
    }
    return normalized;
}

function set(metric: any) {
    let url = document.location.href

    // metric 내부의 number 객체들에 대한 소수점 4자리 제한
    const normalizedMetric = normalizeEntry(metric);
    const {name: metricName, value: metricValue, id: metricId} = normalizedMetric as MetricObject;

    let timestamp: number = 0;
    if (typeof metricId === 'string' && metricId.length > 0) {
        timestamp = Number(metricId.split('-')[1]);
    }

    send('web-vital', makeSendData({
        json: {
            url: url,
            value: normalizedMetric as Object
        },
        logType: maxyLogType.T_WebNav_Web_Vital.hexCode,
        iTime: 0,
        logTm: timestamp,
        mapmStr: ''
    }));

    // pageInfo에 vital정보추가
    if (typeof metricName === 'string' && metricName.length > 0) {
        let pgAry = pageInfo.getPage();
        if (pgAry && pgAry.length > 0) {
            const idx = pgAry.length-1;
            if (metricName.toLowerCase() === 'cls') {
                pgAry[idx].vt.cls = metricValue;
            } else if (metricName.toLowerCase() === 'fcp') {
                pgAry[idx].vt.fcp = metricValue;
            } else if (metricName.toLowerCase() === 'inp') {
                pgAry[idx].vt.inp = metricValue;
            } else if (metricName.toLowerCase() === 'lcp') {
                pgAry[idx].vt.lcp = metricValue;
            } else if (metricName.toLowerCase() === 'ttfb') {
                pgAry[idx].vt.ttfb = metricValue;
            }
            pageInfo.setPage(pgAry);
        } else {
            console.warn('[MF] pageInfo.getPage() returned invalid data:', pgAry);
        }
    }
}

export function initWebVitalsCollector() {
    // FID는 INP로 대체됨
    // onFID((metric) => send('web-vital', metric));

    /**
     * CLS (Cumulative Layout Shift)
     * 정의: 페이지가 시각적으로 예기치 않게 이동하는 정도(레이아웃 이동)를 측정합니다.
     * 목적: 사용자가 페이지를 보는 도중에 요소가 갑자기 이동해 불편을 주는 문제를 파악합니다.
     * 예시: 이미지가 늦게 로드되면서 텍스트가 밀리는 경우.
     */
    onCLS((metric) => set(metric));

    /**
     * FCP (First Contentful Paint)
     * 정의: 브라우저가 처음으로 텍스트, 이미지, canvas 등 실제 콘텐츠를 그린 시점까지의 시간입니다.
     * 목적: 사용자에게 "무언가 나타났다"는 피드백이 얼마나 빨리 제공되는지를 측정합니다.
     * 예시: 로딩 중 흰 화면이 아닌 첫 UI 요소가 등장한 시점.
     */
    onFCP((metric) => set(metric));

    /**
     * INP (Interaction to Next Paint)
     * 정의: 사용자 입력(클릭, 탭, 키 입력 등)에 대한 반응 시간이 얼마나 안정적인지를 측정합니다.
     * 목적: 실사용자의 전체 상호작용 중 가장 느린 반응 시간을 기반으로 앱의 상호작용 성능을 평가합니다.
     * FID 를 대체하는 지표이며, 페이지 전체에서 발생한 모든 상호작용 중 가장 느린 반응 시간을 바탕으로 실질적인 사용자 경험 품질을 더 정확히 반영합니다.
     * 예시: 버튼을 클릭했을 때 화면이 바뀌기까지의 지연.
     */
    onINP((metric) => set(metric));

    /**
     * LCP (Largest Contentful Paint)
     * 정의: 뷰포트 내 가장 큰 이미지나 텍스트 블록이 렌더링된 시점까지의 시간입니다.
     * 목적: 사용자가 “주요 콘텐츠가 표시됐다”고 느끼는 시점을 파악합니다.
     * 예시: 메인 배너 이미지, 큰 타이틀 등.
     */
    onLCP((metric) => set(metric));

    /**
     * TTFB (Time To First Byte)
     * 정의: 브라우저가 서버로부터 첫 번째 바이트를 수신할 때까지의 시간.
     * 목적: 서버 응답 속도 및 네트워크 지연을 측정합니다.
     * 예시: HTTP 요청 → 서버 처리 → 첫 바이트 수신까지의 시간.
     */
    onTTFB((metric) => set(metric));
}

