import {makeSendData, send} from "../utils/sender";
import {maxyLogType} from "../utils/logType";

/**
 * window.onerror 이벤트를 처리하여 에러 정보를 수집/전송합니다.
 * @param event 브라우저 ErrorEvent
 * @returns void
 */
function onError(event: any): void {
    console.log(`[MF]  ERROR!!!!!!!!!!!!!!, ${event}`)
    if (event instanceof ErrorEvent) {
        console.log(`[MF] [Err] event type ErrorEvent`)
        // 실행 중 발생한 JS 에러
        //console.error("[MF] [JS Error]", event.message, event.filename, event.lineno, event.error);
        // 이벤트를 문자열로 변환 (디버깅용)
        //const eventToMsg: string = event ? JSON.stringify(event) : '';

        // 기본 메시지
        const message: string = event?.message ?? '';

        // 에러 객체에서 메시지와 스택 추출
        const errorMessage: string = event.error?.message ?? '';
        const errorStack: string = event.error?.stack ?? '';

        // 전송할 메시지 조합
        const sendMessage: string = `${message} ${errorMessage} ${errorStack}`;

        // 공백 제거 후 유효성 체크
        const trimmed = sendMessage.replace(/\s/g, '');

        if (trimmed !== '' && trimmed !== 'Scripterror.') {
            send('error', makeSendData({
                json: {
                    value: sendMessage
                },
                logType: maxyLogType.T_WebNav_Java_Error.hexCode,
                iTime: 0,
                logTm: Math.floor(performance.timeOrigin + event.timeStamp),
                mapmStr: ""
            }));
        }
    } else {
        console.log(`[MF] [Err] event type Event`)
        // 리소스 로드 실패 (예: <script>, <link>, <img> 등)
        const target = event.target as HTMLElement;
        if (target && (target.tagName === "SCRIPT" || target.tagName === "LINK" || target.tagName === "IMG")) {
            //console.error("[Resource Load Error]", target.tagName, (target as any).src || (target as any).href);
            const sendMessage: string = `${target.tagName}, ${(target as any).src || (target as any).href}`;
            send('error', makeSendData({
                json: {
                    value: sendMessage
                },
                logType: maxyLogType.T_WebNav_Java_Error.hexCode,
                iTime: 0,
                logTm: Math.floor(performance.timeOrigin + event.timeStamp),
                mapmStr: ""
            }));
        } else {
            console.log(`[Err] event type Unknown`)
            //console.error("[Unknown Error Event]", event);
            send('error', makeSendData({
                json: {
                    value: `Unknown Error Event, ${event}`
                },
                logType: maxyLogType.T_WebNav_Java_Error.hexCode,
                iTime: 0,
                logTm: Math.floor(performance.timeOrigin + event.timeStamp),
                mapmStr: ""
            }));
        }
    }
}

function onUnhandledRejection(event: PromiseRejectionEvent) {
    send('error', makeSendData({
        json: {
            value: event.reason
        },
        logType: maxyLogType.T_WebNav_Java_Error.hexCode,
        iTime: 0,
        logTm: Math.floor(performance.timeOrigin + event.timeStamp),
        mapmStr: ""
    }));
}

/**
 * 전역 에러 수집기를 초기화합니다. window.error 이벤트를 구독합니다.
 * @returns void
 */
export function initErrorCollector() {
    // Resource(script) load error 전용 Listener
    document.addEventListener("error", (event: any) => {
        console.log(`get maxy event listener - document(error)`)
        onError(event);
    }, true ); // 반드시 capture phase

    window.addEventListener('error', (event: any) => {
        console.log(`get maxy event listener - window(error)`)
        onError(event)
    }, false);

    window.addEventListener("unhandledrejection", (event) => {
        console.log(`get maxy event listener - unhandledrejection`)
        //console.warn("처리되지 않은 Promise 거부:", event.reason);
        onUnhandledRejection(event)
    }, false);

}

