import {
    gAppId,
    gEndPoint,
    gServerType,
    gPageId,
    gIsSPA,
    gUsePopstatePageStart,
    gDeviceInfo, gPingIntervalStopFunc, setPingIntervalStopFunc, gTotalIntervalStopFunc, setTotalIntervalStopFunc,
    gPageIntervalStopFunc, setPageIntervalStopFunc, setSReplayIntervalStopFunc, gSReplayIntervalStopFunc
} from "../globals";
import { endMaxyPage, startMaxyPage } from "./router";
import {makeSendData, onBeforeUnload, scheduleSender, send} from "../utils/sender";
import { getOrCreateSessionUUID } from "../utils/uuid";
import { maxyLogType } from "../utils/logType";
