import {AppInfoDto, DeviceInfo, gAppId, gServerType} from "../globals";

const KEY_MAXY_LOGIN_YN = `mf-login-yn-${gAppId}-${gServerType}`;
const KEY_MAXY_USER_ID = `mf-user-id-${gAppId}-${gServerType}`;
const KEY_MAXY_APP_START_ORIGIN = `mf-app-start-origin-${gAppId}-${gServerType}`;
const KEY_MAXY_APP_INFO = `mf-app-info-${gAppId}-${gServerType}`;
const KEY_MAXY_DEVICE_INFO = `mf-dvc-info-${gAppId}-${gServerType}`;
const KEY_MAXY_PAGE_FLOW_ORDER = `mf-pg-flow-order-${gAppId}-${gServerType}`;
const KEY_MAXY_PAGE_ERR_CNT= `mf-pg-err-cnt-${gAppId}-${gServerType}`;
const KEY_MAXY_PAGE_LOG_CNT= `mf-pg-log-cnt-${gAppId}-${gServerType}`;
const KEY_MAXY_PAGE_EVT_CNT= `mf-pg-evt-cnt-${gAppId}-${gServerType}`;
const KEY_MAXY_PAGE_REQ_CNT= `mf-pg-req-cnt-${gAppId}-${gServerType}`;
const KEY_MAXY_PAGE_REQ_TOT_TIME= `mf-pg-req-tot-${gAppId}-${gServerType}`;
const KEY_MAXY_PARENT_LOG_DATE = `mf-parent-log-date-${gAppId}-${gServerType}`;

export function setMaxyLoginComplete() {
    safeSetItemSession(KEY_MAXY_LOGIN_YN, 'Y');
}
export function setMaxyLogoutComplete() {
    safeSetItemSession(KEY_MAXY_LOGIN_YN, 'N');
}

export function getLoginYn(): string {
    let loginYn = sessionStorage.getItem(KEY_MAXY_LOGIN_YN);
    if (loginYn === undefined || loginYn === null) {
        safeSetItemSession(KEY_MAXY_LOGIN_YN,'N');
        loginYn = 'N';
    }
    return loginYn;
}

export function setUserIdInMaxy(userId: string) {
    safeSetItemSession(KEY_MAXY_USER_ID, userId);
}
export function getUserIdInMaxy() {
    let userId = sessionStorage.getItem(KEY_MAXY_USER_ID);
    if (userId === undefined || userId === null)
        userId = '';
    return userId;
}

export function getStartedApp(): boolean {
    let startedOrigin = sessionStorage.getItem(KEY_MAXY_APP_START_ORIGIN);
    if (startedOrigin === null) {
        safeSetItemSession(KEY_MAXY_APP_START_ORIGIN, location.origin);
        return false;
    } else {
        return location.href.indexOf(startedOrigin) > -1;
    }
}

export function setCookie(name: string, value: string, days: number) {
    const expires = new Date(Date.now() + days * 864e5).toUTCString();
    document.cookie = `${name}=${value}; expires=${expires}; path=/`;
}
export function getCookie(name: string): string | null {
    const match = document.cookie.match(new RegExp('(^| )' + name + '=([^;]+)'));
    return match?.[2] ?? null;
}

export function setAppInfoData(data: AppInfoDto) {
    safeSetItemSession(KEY_MAXY_APP_INFO, JSON.stringify(data));
}
export function getAppInfoData(): AppInfoDto|null {
    let appInfo = sessionStorage.getItem(KEY_MAXY_APP_INFO);
    if (!appInfo) {
        return null;
    }
    return JSON.parse(appInfo);
}

export function setDeviceInfoData(data: DeviceInfo) {
    safeSetItemSession(KEY_MAXY_DEVICE_INFO, JSON.stringify(data));
}
export function getDeviceInfoData(): DeviceInfo|null {
    let devInfo = sessionStorage.getItem(KEY_MAXY_DEVICE_INFO);
    if (!devInfo) {
        return null;
    }
    return JSON.parse(devInfo);
}

export function setPageFlowOrder(num: number) {
    safeSetItemSession(KEY_MAXY_PAGE_FLOW_ORDER, num.toString());
}
export function getPageFlowOrder() {
    return Number(sessionStorage.getItem(KEY_MAXY_PAGE_FLOW_ORDER) || 1);
}

export function setParentLogDate(num: number) {
    safeSetItemSession(KEY_MAXY_PARENT_LOG_DATE, num.toString());
}
export function getParentLogDate() {
    return Number(sessionStorage.getItem(KEY_MAXY_PARENT_LOG_DATE) || 0);
}

export function setPageErrCnt(num: number) {
    safeSetItemSession(KEY_MAXY_PAGE_ERR_CNT, num.toString());
}
export function getPageErrCnt() {
    return Number(sessionStorage.getItem(KEY_MAXY_PAGE_ERR_CNT) || 0);
}

export function setPageLogCnt(num: number) {
    safeSetItemSession(KEY_MAXY_PAGE_LOG_CNT, num.toString());
}
export function getPageLogCnt() {
    return Number(sessionStorage.getItem(KEY_MAXY_PAGE_LOG_CNT) || 1);
}

export function setPageEvtCnt(num: number) {
    safeSetItemSession(KEY_MAXY_PAGE_EVT_CNT, num.toString());
}
export function getPageEvtCnt() {
    return Number(sessionStorage.getItem(KEY_MAXY_PAGE_EVT_CNT) || 0);
}

export function setPageReqCnt(num: number) {
    safeSetItemSession(KEY_MAXY_PAGE_REQ_CNT, num.toString());
}
export function getPageReqCnt() {
    return Number(sessionStorage.getItem(KEY_MAXY_PAGE_REQ_CNT) || 0);
}

export function setPageReqTotal(num: number) {
    safeSetItemSession(KEY_MAXY_PAGE_REQ_TOT_TIME, num.toString());
}
export function getPageReqTotal() {
    return Number(sessionStorage.getItem(KEY_MAXY_PAGE_REQ_TOT_TIME) || 0);
}

export function safeSetItemSession(key: string, value: string): boolean {
    try {
        sessionStorage.setItem(key, value);
        return true;
    } catch (e) {
        if (isQuotaExceeded(e)) {
            console.warn(`sessionStorage 공간이 가득 찼습니다.`);
        } else {
            console.error(`sessionStorage 저장 중 에러: ${(e as Error).message}`);
        }
        return false;
    }
}

export function safeSetItemLocal(key: string, value: string): boolean {
    try {
        localStorage.setItem(key, value);
        return true;
    } catch (e) {
        if (isQuotaExceeded(e)) {
            console.warn(`localStorage 공간이 가득 찼습니다.`);
        } else {
            console.error(`localStorage 저장 중 에러: ${(e as Error).message}`);
        }
        return false;
    }
}

function isQuotaExceeded(e: any): boolean {
    if (!e) return false;
    if (e.code === 22) return true; // Chrome, Firefox
    if (e.code === 1014 && e.name === "NS_ERROR_DOM_QUOTA_REACHED") return true; // Firefox
    if (e.name === "QuotaExceededError") return true; // Safari 등
    return false;
}
