import { send } from "../utils/sender";
import { toBase64 } from "fast-base64";
// ---- rrweb core 로드 ----
import * as rrweb from 'rrweb';
import pako from 'pako';

// ---- Types and Interfaces ----
interface RRWebEvent {
    type: number;
    data: any;
    timestamp: number;
}

interface RRWebAPI {
    record: (options: any) => (() => void) | undefined;
    pack: (events: RRWebEvent[]) => any;
}

// Extend Window interface to include rrweb and custom functions
declare global {
    interface Window {
        rrweb?: RRWebAPI;
        crypto?: Crypto & {
            randomUUID?: () => string;
        };
    }
}

function startRecording(): void {
    if (!rrweb) {
        console.error("rrweb 로드 실패");
        return;
    }

    rrweb.record({
        async emit(event: RRWebEvent) {

            const str = JSON.stringify(event);

            // fast-base64사용
            const compressed = pako.deflate(str);
            await toBase64(compressed).then(base64Compressed => {
                let sendData: any[];
                sendData = [
                    event.timestamp,
                    base64Compressed,
                ]
                send('session-play', sendData.join('|'));
            });

        },
        //blockClass: "rr-block",
        //ignoreClass: "rr-ignore",
        maskAllInputs: true,
        //maskTextClass: "rr-mask",
        checkoutEveryNth: 500,
        // 마우스 움직임 샘플링 - 더 자주 기록하여 부드러운 재생
        sampling: {
            mousemove: 20,  // 20ms마다 마우스 움직임 기록 (더 부드러운 재생)
            mouseInteraction: true,  // 마우스 클릭, 스크롤 등 상호작용 기록
            scroll: 200,  // 스크롤 이벤트 샘플링
            input: 'last'  // 입력 이벤트 기록
        },
        // 추가 레코딩 옵션
        //recordCanvas: true,  // 캔버스 요소 기록
        //collectFonts: true,  // 폰트 정보 수집
        // 성능 최적화
        slimDOMOptions: {
            script: true,
            comment: true,
            headFavicon: true,
            headWhitespace: true,
            headMetaDescKeywords: true,
            headMetaSocial: true,
            headMetaRobots: true,
            headMetaHttpEquiv: true,
            headMetaAuthorship: true,
            headMetaVerification: true
        }
    });
}

export function initSessionReplayCollector(): void {
    startRecording();
}